@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #0c0b0b;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: "Steiner";
  src: url("./Assets/Fonts/Steiner/steiner-premium-regular.ttf")
    format("truetype");
}

@font-face {
  font-family: "Inter";
  src: url("./Assets/Fonts/Inter-font-family/static/Inter-Regular.ttf")
    format("truetype");
}

.steiner-font {
  font-family: "Steiner";
  font-weight: 400;
  font-style: normal;
}

.inter-font {
  font-family: "Inter";
  font-weight: 400;
  font-style: normal;
}

.connectwallet-card {
  background: linear-gradient(
    180deg,
    rgba(36, 35, 35, 0.8) 0%,
    rgba(36, 35, 35, 0.64) 100%
  );
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.btn-fill {
  background: linear-gradient(180deg, #a08127 0%, #bc962c 100%);
  color: white;
  font-family: "Steiner";
  font-weight: 400;
  font-style: normal;
}

.nft-card-filter {
  background: rgba(36, 35, 35, 0.8);
  background-blend-mode: multiply;
  backdrop-filter: blur(5px);
  border-radius: 0px 0px 8px 8px;
}

.nft-name {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  color: white;
}

.nft-upgrade-button {
  padding: 12px 20px;
  background: #cdad53;
  border-radius: 8px;
  color: white;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  white-space: nowrap;
}
